<template>
  <div class="error-page">
    <div class="content-con">
      <img :src="src" alt="404">
      <!--<div class="text-con">-->
        <!--<h4>{{ code }}</h4>-->
        <!--<h5>{{ desc }}</h5>-->
      <!--</div>-->
      <div class="text-con1 text-center margin-t-20">
        {{ code }} {{ desc }}
      </div>
      <div>
        <Button size="large" type="text" @click="backHome">返回首页</Button>
        <Button size="large" type="text" @click="backPrev">返回上一页({{ second }}s)</Button>
      </div>
    </div>
  </div>
</template>

<script>
  import error404 from '@/assets/img/error-404.png'
  export default {
    name: 'error_404',
    components: {},
    data() {
      return {
        src: error404,
        code: '404',
        desc: "Oh~~您的页面好像飞走了~",
        second: 3,
        timer: null
      }
    },
    methods: {
      backHome() {
        this.$router.replace({
          name: 'orderList'
        })
      },
      backPrev() {
        this.$router.back()
      }
    },
    mounted() {
      this.timer = setInterval(() => {
        if(this.second === 0) this.backPrev()
        else this.second--
      }, 1000)
    },
    beforeDestroy() {
      clearInterval(this.timer)
    }
  }
</script>
<style lang="less" scoped>
  .error-page {
    width: 100%;
    height: 100%;
    position: relative;
    background: #f8f8f9;
    .content-con {
      width: 350px;
      height: 300px;
      position: fixed;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -60%);
      img {
        display: block;
        width: 100%;
        height: 100%;
      }
      .text-con {
        position: absolute;
        left: 0px;
        top: 0px;
        h4 {
          position: absolute;
          left: 0px;
          top: 0px;
          font-size: 80px;
          font-weight: 700;
          color: #348EED;
        }
        h5 {
          position: absolute;
          width: 700px;
          left: 0px;
          top: 100px;
          font-size: 20px;
          font-weight: 700;
          color: #67647D;
        }
      }
      .text-con1{
        line-height: 30px;
        color: #999999;
        font-size: 16px;
      }
      .back-btn-group {
        position: absolute;
        right: 0px;
        bottom: 20px;
      }
    }
  }
</style>


